/* Reset and base styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
    background: linear-gradient(135deg, #f0fdf4 0%, #ecfdf5 50%, #d1fae5 100%);
    min-height: 100vh;
    overflow-x: hidden;
}

/* Main container */
.auth-container {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    position: relative;
}

/* Auth card */
.auth-card {
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(20px);
    border-radius: 24px;
    box-shadow: 
        0 25px 50px -12px rgba(16, 185, 129, 0.15),
        0 0 0 1px rgba(16, 185, 129, 0.05),
        inset 0 1px 0 0 rgba(255, 255, 255, 0.1);
    padding: 40px;
    width: 100%;
    max-width: 420px;
    position: relative;
    z-index: 10;
    border: 1px solid rgba(16, 185, 129, 0.1);
    animation: slideUp 0.6s ease-out;
}

/* Header */
.auth-header {
    text-align: center;
    margin-bottom: 32px;
}

.logo {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    margin-bottom: 16px;
}

.logo-icon {
    width: 48px;
    height: 48px;
    background: linear-gradient(135deg, #10b981 0%, #34d399 100%);
    border-radius: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    font-size: 16px;
    color: white;
    box-shadow: 0 8px 25px rgba(16, 185, 129, 0.3);
}

.logo h1 {
    font-size: 28px;
    font-weight: 700;
    background: linear-gradient(135deg, #10b981 0%, #059669 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    letter-spacing: -0.02em;
}

.auth-subtitle {
    color: #6b7280;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
}

/* Auth content */
.auth-content {
    margin-bottom: 24px;
}

/* Loading */
.loading {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 40px;
    color: #10b981;
    font-size: 16px;
}

.loading::after {
    content: '';
    width: 20px;
    height: 20px;
    margin-left: 10px;
    border: 2px solid transparent;
    border-top: 2px solid #10b981;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

/* Footer */
.auth-footer {
    text-align: center;
    margin-top: 32px;
    padding-top: 24px;
    border-top: 1px solid rgba(16, 185, 129, 0.1);
}

.auth-footer p {
    color: #9ca3af;
    font-size: 12px;
    font-weight: 400;
}

/* Background decorations */
.bg-decoration {
    position: absolute;
    inset: 0;
    pointer-events: none;
    overflow: hidden;
}

.circle {
    position: absolute;
    border-radius: 50%;
    background: linear-gradient(135deg, rgba(16, 185, 129, 0.1) 0%, rgba(52, 211, 153, 0.05) 100%);
    animation: float 6s ease-in-out infinite;
}

.circle-1 {
    width: 300px;
    height: 300px;
    top: -150px;
    right: -150px;
    animation-delay: 0s;
}

.circle-2 {
    width: 200px;
    height: 200px;
    bottom: -100px;
    left: -100px;
    animation-delay: 2s;
}

.circle-3 {
    width: 150px;
    height: 150px;
    top: 50%;
    right: -75px;
    animation-delay: 4s;
}

/* Clerk component styling overrides */
.clerk-card {
    background: transparent !important;
    box-shadow: none !important;
    border: none !important;
    padding: 0 !important;
}

.clerk-title {
    color: #111827 !important;
    font-weight: 600 !important;
    font-size: 20px !important;
    text-align: center !important;
    margin-bottom: 16px !important;
}

.clerk-input {
    background: #f8fafc !important;
    border: 2px solid #e5e7eb !important;
    border-radius: 12px !important;
    padding: 14px 16px !important;
    font-size: 16px !important;
    transition: all 0.2s ease !important;
    color: #374151 !important;
}

.clerk-input:focus {
    border-color: #10b981 !important;
    box-shadow: 0 0 0 3px rgba(16, 185, 129, 0.1) !important;
    background: #ffffff !important;
}

.clerk-button {
    background: linear-gradient(135deg, #10b981 0%, #34d399 100%) !important;
    border: none !important;
    border-radius: 12px !important;
    padding: 14px 24px !important;
    font-weight: 600 !important;
    font-size: 16px !important;
    color: white !important;
    cursor: pointer !important;
    transition: all 0.2s ease !important;
    width: 100% !important;
    box-shadow: 0 4px 15px rgba(16, 185, 129, 0.3) !important;
}

.clerk-button:hover {
    transform: translateY(-2px) !important;
    box-shadow: 0 8px 25px rgba(16, 185, 129, 0.4) !important;
}

/* Global Clerk styling */
:global(.cl-rootBox) {
    font-family: 'Inter', sans-serif !important;
}

:global(.cl-card) {
    background: transparent !important;
    border: none !important;
    box-shadow: none !important;
}

:global(.cl-headerTitle) {
    color: #111827 !important;
    font-weight: 600 !important;
    font-size: 20px !important;
}

:global(.cl-headerSubtitle) {
    color: #6b7280 !important;
    font-size: 14px !important;
}

:global(.cl-formFieldInput) {
    background: #f8fafc !important;
    border: 2px solid #e5e7eb !important;
    border-radius: 12px !important;
    padding: 14px 16px !important;
    font-size: 16px !important;
    transition: all 0.2s ease !important;
}

:global(.cl-formFieldInput:focus) {
    border-color: #10b981 !important;
    box-shadow: 0 0 0 3px rgba(16, 185, 129, 0.1) !important;
    background: #ffffff !important;
}

:global(.cl-formButtonPrimary) {
    background: linear-gradient(135deg, #10b981 0%, #34d399 100%) !important;
    border: none !important;
    border-radius: 12px !important;
    padding: 14px 24px !important;
    font-weight: 600 !important;
    font-size: 16px !important;
    height: auto !important;
    box-shadow: 0 4px 15px rgba(16, 185, 129, 0.3) !important;
}

:global(.cl-formButtonPrimary:hover) {
    transform: translateY(-2px) !important;
    box-shadow: 0 8px 25px rgba(16, 185, 129, 0.4) !important;
}

:global(.cl-socialButtonsBlockButton) {
    border: 2px solid #e5e7eb !important;
    border-radius: 12px !important;
    background: #ffffff !important;
    transition: all 0.2s ease !important;
}

:global(.cl-socialButtonsBlockButton:hover) {
    border-color: #10b981 !important;
    background: #f0fdf4 !important;
}

:global(.cl-dividerLine) {
    background: rgba(16, 185, 129, 0.2) !important;
}

:global(.cl-dividerText) {
    color: #6b7280 !important;
    background: #ffffff !important;
}

:global(.cl-footerActionLink) {
    color: #10b981 !important;
}

:global(.cl-footerActionLink:hover) {
    color: #059669 !important;
}

/* Animations */
@keyframes slideUp {
    from {
        opacity: 0;
        transform: translateY(30px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes float {
    0%, 100% { transform: translateY(0px) rotate(0deg); }
    50% { transform: translateY(-20px) rotate(180deg); }
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

/* Responsive design */
@media (max-width: 768px) {
    .auth-card {
        padding: 32px 24px;
        margin: 16px;
        border-radius: 20px;
    }
    
    .logo h1 {
        font-size: 24px;
    }
    
    .logo-icon {
        width: 40px;
        height: 40px;
        font-size: 14px;
        border-radius: 12px;
    }
    
    .circle-1 {
        width: 200px;
        height: 200px;
        top: -100px;
        right: -100px;
    }
    
    .circle-2 {
        width: 150px;
        height: 150px;
        bottom: -75px;
        left: -75px;
    }
    
    .circle-3 {
        display: none;
    }
}

@media (max-width: 480px) {
    .auth-card {
        padding: 24px 20px;
    }
    
    .auth-header {
        margin-bottom: 24px;
    }
    
    .logo {
        gap: 8px;
        margin-bottom: 12px;
    }
    
    .logo h1 {
        font-size: 22px;
    }
    
    .logo-icon {
        width: 36px;
        height: 36px;
        font-size: 12px;
    }
}
